<template>
	<view class="menu">
		<view class="" @click="enter(index)" v-for="(item,index) in menu" :key='index'>
			<view class="pic">
				<image class="img" :src=item.imgurl mode=""></image>
			</view>
			<view class="">
				<text>{{item.title}}</text>
			</view>
		</view>
	</view>
</template>
<script>
	export default{
		data(){
			return{
				menu:[
					{imgurl:'../../static/image/pic1.png',title:'公开模型'},
					{imgurl:'../../static/image/pic2.png',title:'设备管理'},
					{imgurl:'../../static/image/pic3.png',title:'树蚁资讯'},
					{imgurl:'../../static/image/pic4.png',title:'订单管理'},
					// {imgurl:'../../static/image/pic5.png',title:'敬请期待'},
				]
			}
		},
		computed: {
			
		},
		methods:{
			enter(index){
				 if(index==0){
					 uni.navigateTo({
					 	url:'/pages/publicModel/publicModel'
					 })
				}else if(index==1){
					uni.navigateTo({
						url:'/pages/deviceManage/deviceManage'
					})
				}else if(index==2){
					uni.navigateTo({
						url:'/pages/news/news'
					})
				}else if(index==3){//订单管理
					uni.navigateTo({
						url:'/pages/orderManage/orderManage'
					})
				}
				else if(index==4){
					uni.showToast({
						title:'开发中',
						icon:'none'
					})
				}
			}
		}
	}
</script>

<style scoped>
	.menu{
		display:flex;
		align-items: center;
		justify-content: space-between;
		font-size: 24rpx;
		color: #000000;
	}
	.pic{
		height: 80rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 4rpx;
	}
	.menu :nth-child(1) .img{
		width: 56rpx;
		height: 56rpx;
	}
	.menu :nth-child(2) .img{
		width:70rpx ;
		height: 70rpx;
	}
	.menu :nth-child(3) .img{
		width:58rpx ;
		height: 58rpx;
	}
	.menu :nth-child(4) .img{
		width:60rpx ;
		height: 60rpx;
	}
	.menu :nth-child(5) .img{
		width: 56rpx;
		height: 56rpx;
	}
	
</style>
